$orbitCount: 22;
$radius: 1012px;

.container {
  margin: 0 auto;
  width: 55px;
  height: 55px;
  position: relative;
  perspective: $radius;
  position: absolute;
  left: 50%;
  top: -10%;
  transform: translate(-50%, -50%);
}

.carousel {
  width: 100%;
  height: 100%;
  position: absolute;
  transform-style: preserve-3d;
  // 运动轨迹动画
  .carousel_field_wrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    @for $i from 1 through $orbitCount + 1 {
      &:nth-child(#{$i}) {
        // 倾斜角度
        transform: rotateZ(#{($i - 2) * 16.36}deg);
        // 共13个元素(figure)
        .carousel_field.satellite {
          img {
            // transform: rotateZ(#{1 + ($i - 2) * (360 / $orbitCount) * -1}deg); // 旋转一段时间就变歪
            // transform: rotateZ(-160deg); // 左边是正角度
            transform: rotateZ(-90deg);
          }

          // 外旋转
          animation: orbit 100000ms linear infinite;
        }
      }
    }
  }
  .carousel_field {
    position: absolute;
    top: 50%;
    left: 50%;
    // 旋转的直径
    width: $radius;
    transform: translate(-50%, -50%);
    transform-style: preserve-3d;
    img {
      position: absolute;
      top: 50%;
      left: 50%;
      box-sizing: border-box;
    }
  }
  // sun 图标
  .mother img {
    width: 218px;
    transform: translate(-50%, -50%);
    opacity: 0;
  }
  // 卫星 图标
  .satellite img {
    left: 100%;
    width: 75px;
    transform: translate(-50%, -50%);
    // transform: rotateZ(195deg);
