@import '../../assets/css/mixins';
.dao_particles_container {
  position: relative;
  height: 594px;
  overflow: hidden;
  @include webpSupportBackgroundImgPc('dark', true, 'dao/ribbon/ribbon');
  @include webpSupportBackgroundImgPc('light', true, 'dao/ribbon/ribbon_light');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  &::before {
    @include darkThemeOnly {
      background: linear-gradient(to bottom, #1e132f, #1e132f 5%, rgba(255, 255, 255, 0));
    }
    @include lightThemeOnly {
      background: linear-gradient(to bottom, #f2f0f4, #f2f0f4 5%, rgba(255, 255, 255, 0));
    }

    content: '';
    width: 100%;
    height: 150px;
    position: absolute;
    z-index: 2;
    pointer-events: none;
    touch-action: none;
  }
  &::after {
    @include darkThemeOnly {
      background: linear-gradient(to top, #1e132f, #1e132f 30%, rgba(255, 255, 255, 0));
    }
    @include lightThemeOnly {
      background: linear-gradient(to top, #f2f0f4, #f2f0f4 30%, rgba(255, 255, 255, 0));
    }
    content: '';
    width: 100%;
    height: 240px;
    position: absolute;
    z-index: 2;
    pointer-events: none;
    touch-action: none;
    bottom: 0;
  }
}
.dao_content {
  @include common-content-width;
}
.dao_home_mobile {
  background-position: top center;
  background-size: contain;
  background-repeat: no-repeat;
  @include webpSupportBackgroundImgMobile('dark', true, 'dao/ribbon/ribbon_m');
  @include webpSupportBackgroundImgMobile('light', true, 'dao/ribbon/ribbon_m_light');
}
