@import '../../../assets/css/theme';
@import '../../../assets/css/mixins';

$tab_active_color: #8c51ff;
$tab_bg_dark: rgba(231, 222, 255, 0.1);
$tab_bg_light: rgba(255, 255, 255, 0.5);
$tab_border_color_dark: rgba(255, 255, 255, 0.1);
$tab_border_color_light: #fff;

.simple_tab {
  padding: 10px 12px 16px;
  background: $tab_bg_dark;
  border-radius: 10px 10px 9px 9px;
  border: 1px solid $tab_border_color_dark;
  backdrop-filter: blur(12px);
  @include lightThemeOnly {
    border: 1px solid $tab_border_color_light;
    background: $tab_bg_light;
  }

  &_nav {
    display: flex;
    &_item {
      font-family: PingFangSC, PingFang SC, sans-serif;
      font-weight: 400;
      font-size: 14px;
      color: $color-text-default-5;
      line-height: 20px;
      text-align: left;
      font-style: normal;
      margin-right: 16px;
      cursor: pointer;
      position: relative;
      height: 20px;

      &:last-child {
        margin-right: 0;
      }
    }
    &_item_active {
      color: $color-text-default;
      font-weight: 600;
      &:after {
        content: '';
        position: absolute;
        bottom: -4px;
        left: 50%;
        transform: translateX(-15px);
        transition: left ease 500ms;
        width: 30px;
        height: 4px;
        border-radius: 2px;
        background: $tab_active_color;
      }
    }
  }
  &_content {
    color: $color-text-default;
    padding-top: 30px;
  }
}
