@import '../../../assets/css/theme';
@import '../../../assets/css/mixins';

.dao_title_pc {
  position: relative;
  display: flex;
  flex-flow: column;
  align-items: center;
  margin-top: 50px;
  &_img {
    margin-bottom: 22px;
    img:nth-child(1) {
      height: 70px;
    }
    img:nth-child(2) {
      width: 216px;
      height: 237px;
    }
    img:nth-child(3) {
      height: 70px;
    }
  }
  &_text {
    h1 {
      font-family: Helvetica, Helvetica;
      font-weight: bold;
      font-size: 40px;
      color: $color-text-default;
      line-height: 48px;
      margin-bottom: 22px;
    }
    h2 {
      font-family: Helvetica;
      font-size: 20px;
      color: $color-text-default;
      line-height: 24px;
      text-align: center;
    }
  }
}

.dao_title_m {
  @include common-content-width;
  text-align: center;
  color: $color-text-default;
  font-family: Helvetica, Helvetica, sans-serif;
  margin-bottom: 100px;
  @include mobile-only {
    margin-bottom: 30px;
  }
  h1 {
    font-size: 36px;
    display: none;
    @include mobile-only {
      display: block;
    }
  }
  h2 {
    font-size: 60px;
    margin-bottom: 30px;
    @include mobile-only {
      font-size: 26px;
      margin-bottom: 20px;
      margin-top: 12px;
    }
  }
  h3 {
    font-size: 46px;
    font-weight: normal;
    @include mobile-only {
      font-size: 16px;
    }
  }
}
