@import '../../../assets/css/mixins';
@import '../../../assets/css/theme';

.graphics {
  &_wrap {
    display: flex;
    color: $color-text-default;
    align-items: center;
  }
  &_arrow {
    margin-bottom: 100px;
    height: 500px;
    width: calc(100vw - (100vw - 1200px) / 2);
    background-size: auto 100%;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    box-sizing: border-box;

    &_ball_1,
    &_ball_2,
    &_ball_3 {
      position: absolute;
      img {
        width: 34px;
      }
    }
    &_ball {
      &_1 {
        left: 50px;
        top: 75px;
      }
      &_2 {
        right: 58px;
        top: 50px;
      }
      &_3 {
        right: 47px;
        top: 110px;
      }
    }
    &_coin_1,
    &_coin_2 {
      position: absolute;
      img {
        width: 47px;
      }
    }
    &_coin {
      &_1 {
        right: 147px;
        top: 50px;
      }
      &_2 {
        right: 127px;
        top: 154px;
      }
    }

    &_left {
      margin-right: auto;
      @include webpSupportBackgroundImgPc('dark', true, 'dao/graphics/dao_left_arrow', 'dao/graphics/backlight');
      @include webpSupportBackgroundImgPc('light', true, 'dao/graphics/dao_left_arrow_light', 'dao/graphics/backlight');
      padding-right: 100px;
      padding-left: calc((100vw - 1200px) / 2);
      background-position: right center;
      .graphics_wrap {
        flex-direction: row-reverse;
      }
      .graphics_content_box {
        position: relative;
        margin-left: 87px;
        top: -40px;
      }
    }

    &_right {
      margin-left: auto;
      @include webpSupportBackgroundImgPc('dark', true, 'dao/graphics/dao_right_arrow', 'dao/graphics/backlight');
      @include webpSupportBackgroundImgPc(
        'light',
        true,
        'dao/graphics/dao_right_arrow_light',
        'dao/graphics/backlight'
      );
      padding-left: 100px;
      padding-right: calc((100vw - 1200px) / 2);
      background-position: left center;
      &:first-child {
        .graphics_content_box {
          top: 40px;
        }
      }
      .graphics_content_box {
        // top: 40px;
        margin-right: 87px;
        position: relative;
      }
    }
  }
  &_title {
    font-size: 36px;
    margin-bottom: 16px;
  }
  &_content {
    font-weight: 400;
    font-size: 16px;
    line-height: 28px;
    text-align: left;
    font-style: normal;
    word-break: break-word;
    font-family: PingFangSC, PingFang SC;
    color: $color-text-default-7;
  }
  &_btn_wrap {
    margin-top: 40px;
  }
  &_data {
    width: 370px;
    height: 219px;
    border-radius: 10px;
    border: 4px solid rgba(86, 26, 255, 1);
    position: relative;

    img {
      position: absolute;
      left: -145px;
      top: 73px;
    }
    &_item {
      padding: 40px 0;
      height: 100%;
      margin-left: 220px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    &_label {
      font-family: Helvetica, sans-serif;
      font-size: 14px;
      color: $color-text-default;
      line-height: 17px;
      font-style: normal;
      opacity: 0.5;
    }
    &_value {
      font-family: HeadLineA Regular, sans-serif;
      font-size: 30px;
      color: $color-text-default;
      font-style: normal;
    }
  }
}

.graphics_mini {
  font-family: PingFangSC, PingFang SC, sans-serif;
  &_title {
    font-weight: 600;
    font-size: 20px;
    line-height: 28px;
  }
  &_content {
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    color: $color-text-default-7;
  }
  &_big_img {
    float: right;
  }
  &_btn_wrap {
    margin-top: 16px;
  }
  &_right_content {
    float: right;
    width: 162px;
    margin-top: -48px;
    margin-right: -10px;
    // img {
    //   margin-top: -48px;
    // }
  }
  &_data {
    text-align: left;
    padding-left: 30px;
    margin-bottom: 20px;
  }
  &_label {
    font-family: Helvetica, sans-serif;
    font-size: 12px;
    color: $color-text-default-5;
    line-height: 14px;
  }
  &_value {
    background: linear-gradient(136deg, #b751ff 0%, #6e24ff 100%);
    font-family: HeadLineA Regular, sans-serif;
    font-size: 30px;
    color: transparent;
    background-clip: text;
    -webkit-background-clip: text;
  }
}
