@import '../../../assets/css/theme';
@import '../../../assets/css/mixins';

$m_banner_bg_dark: rgba(255, 255, 255, 0.1);
$m_banner_bg_light: rgba(255, 255, 255, 0.8);
$banner_bg_dark: linear-gradient(to bottom, rgba(90, 92, 255, 0) 0%, rgba(90, 92, 255, 0) 50%, rgb(#a54bff, 0.2) 100%);
$banner_bg_light: linear-gradient(to bottom, rgba(90, 92, 255, 0) 0%, rgba(90, 92, 255, 0) 50%, rgb(#a54bff, 0.1) 100%);
$banner_line_bg_dark: linear-gradient(
  270deg,
  rgba(90, 92, 255, 0) 0%,
  #5a5cff 3%,
  #a54bff 97%,
  rgba(165, 75, 255, 0) 100%
);
$banner_line_bg_light: linear-gradient(
  270deg,
  rgba(90, 92, 255, 0) 0%,
  #5a5cff 3%,
  #a54bff 97%,
  rgba(165, 75, 255, 0) 100%
);
$m_border_color_dark: rgb(255, 255, 255, 0.1);
$m_border_color_light: rgba(51, 51, 51, 0.1);

.dao_banner {
  @include common-content-width;
  display: flex;
  margin-bottom: 200px;
  position: relative;
  &::after {
    content: '';
    position: absolute;
    bottom: -1px;
    width: 0;
    height: 1px;
    background: $banner_line_bg_dark;
    @include lightThemeOnly {
      background: $banner_line_bg_light;
    }
    transition: width ease 500ms;
  }
  &:hover::after {
    width: 100%;
  }
  .banner_item {
    padding: 10px 30px 40px 30px;
    background-size: 100% 200%;
    background-image: $banner_bg_dark;
    @include lightThemeOnly {
      background-image: $banner_bg_light;
    }
    transition: background-position 0.5s;

    img {
      width: 62px;
    }
    &:hover {
      background-position: 0 100%;
    }
  }
  .banner_title {
    color: $color-text-default;
    margin: 30px 0 20px 0;
    font-size: 20px;
    word-break: break-word;
    height: 46px;
  }
  .banner_content {
    color: $color-text-default;
    opacity: 0.5;
    font-size: 12px;
    font-family: PingFangSC, PingFang SC;
    line-height: 20px;
  }
}
.dao_banner_mobile {
  margin-left: 15px;
  margin-right: 15px;
  background: $m_banner_bg_dark;
  border-radius: 10px 10px 9px 9px;
  padding: 12px;
  color: $color-text-default;
  font-family: PingFangSC, PingFang SC, sans-serif;
  margin-bottom: 40px;
  @include lightThemeOnly {
    background: $m_banner_bg_light;
  }
  .banner_item {
    margin-bottom: 12px;
    padding-bottom: 12px;
    border-bottom: 1px solid $m_border_color_dark;
    @include lightThemeOnly {
      border-color: $m_border_color_light;
    }
    &:last-child {
      margin-bottom: 0;
      padding-bottom: 0;
      border: none;
    }
  }
  .banner_title {
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    img {
      width: 30px;
    }
  }
  .banner_content {
    font-weight: 400;
    font-size: 12px;
    color: $color-text-default-5;
    line-height: 20px;
  }
}
