@import '../../../assets/css/var';
@import '../../../assets/css/_mixins';
@import '../../../assets/css/theme';

.marquee_outer {
  margin-top: 100px;
}
.marquee_title {
  font-weight: 600;
  font-size: 30px;
  color: $color-text-default;
  line-height: 42px;
  text-align: center;
  margin-bottom: 20px;
}

.marquee_subtitle {
  font-family: PingFangSC, PingFang SC;
  font-weight: 400;
  font-size: 16px;
  color: $color-text-default;
  line-height: 28px;
  text-align: center;
  margin-bottom: 50px;
  opacity: 0.7;
}

.pc_container {
  backdrop-filter: blur(12px);
  img {
    margin: 50px 0;
    cursor: pointer;
  }
}

.marque_container {
  > div:nth-child(2),
  > div:nth-child(3) {
    @include darkThemeOnly {
      background: rgba(231, 222, 255, 0.05);
      border-top: 1px solid rgba(255, 255, 255, 0.14);
      border-bottom: 1px solid rgba(255, 255, 255, 0.14);
    }
    @include lightThemeOnly {
      background: rgba(255, 255, 255, 0.5);
      border-top: 1px solid rgba(255, 255, 255);
      border-bottom: 1px solid rgba(255, 255, 255);
    }
  }
}

.m_container {
  display: flex;
  flex-wrap: wrap;
  > div {
    width: calc(50% - 2.5px);
    > div {
      display: flex;
      align-items: center;
      transform: scale(0.5);
      width: 200%;
      margin-left: -50%;
      padding-left: 60px;
    }
    img {
      display: block;
      &:first-child {
        margin-right: 16px;
      }
    }
    @include darkThemeOnly {
      background: rgba(231, 222, 255, 0.05);
      border: 1px solid rgba(255, 255, 255, 0.14);
    }
    @include lightThemeOnly {
      background: rgba(255, 255, 255, 0.5);
      border: 1px solid #ffffff;
    }

    border-radius: 2px;
    backdrop-filter: blur(12px);
    margin-bottom: 5px;
    cursor: pointer;
    &:nth-child(2n + 1) {
      margin-right: 5px;
    }
  }
}

@include mobile-only {
  .marquee_outer {
    @include common-content-width;
    margin-top: 0px;
  }
  .marquee_title {
    font-size: 20px;
    line-height: 28px;
    text-align: left;
    margin-bottom: 10px;
  }
  .marquee_subtitle {
    font-size: 12px;
    line-height: 20px;
    text-align: left;
    margin-bottom: 16px;
  }
}
